.battleContainer {
	display: flex;
	justify-content: space-around;
}

.startBattle {
	color: #e6e6e6;
  background: #0a0a0a;
  border: none;
  font-size: 20px;
  border-radius: 3px;
  width: 250px;
  text-align: center;
  display: block;
  padding: 11px 0;
  margin: 10px auto;
	cursor: pointer;
}

.reset {
	font-size: 18px;
	border: none;
	background: transparent;
	color: #d0021b;
	display: block;
	width: 100%;
	text-align: center;
	cursor: pointer;
}

h1 {
	text-align: center;
}